const ZERO = Date.now() + 1000 * 10
let yearElements: NodeListOf<HTMLHeadingElement>




function HSVtoRGB(h: number, s: number, v: number) {
    var r, g, b, i, f, p, q, t;
    i = Math.floor(h * 6);
    f = h * 6 - i;
    p = v * (1 - s);
    q = v * (1 - f * s);
    t = v * (1 - (1 - f) * s);
    switch (i % 6) {
        case 0: r = v, g = t, b = p; break;
        case 1: r = q, g = v, b = p; break;
        case 2: r = p, g = v, b = t; break;
        case 3: r = p, g = q, b = v; break;
        case 4: r = t, g = p, b = v; break;
        case 5: r = v, g = p, b = q; break;
    }
    return {
        r: Math.round(r * 255),
        g: Math.round(g * 255),
        b: Math.round(b * 255)
    };
}



window.onload = () => {
    console.log('loaded')
    let lastTimer = '00:00'
    yearElements = document.querySelectorAll<HTMLHeadingElement>('#fireworks #year h1')

    const updateTimer = () => {
        const secsRemaining = (ZERO - Date.now())
        //console.log(secsRemaining)
        const ssecsRemaining = `${Math.floor(secsRemaining / 60000).toString().padStart(2, '0')}:${Math.floor((secsRemaining / 1000) % 60).toString().padStart(2, '0')}`

        let diffsMap = new Map<number, string>();

        for (let i = 0; i < ssecsRemaining.length; i++) {
            if (ssecsRemaining[i] != lastTimer[i]) {
                diffsMap.set(i, ssecsRemaining[i])
            }
        }

        const allDigits = document.querySelector('#countdown').getElementsByTagName('h1')
        for (const [idx, nChar] of diffsMap) {
            let time = 0.0
            const el = allDigits[idx]
            if (!el) continue
            let switched = false
            const transAnim = () => {
                const y = (time < 0.5 ? time : time - 1.0) * 2.
                //console.log(y)
                el.style.transform = `translateY(${y}em)`
                el.style.opacity = Math.abs(1 - (time * 2)).toString()
                if (time > 0.5 && !switched) {
                    switched = true
                    el.textContent = nChar
                }
                time += 0.05
                if (time <= 1) requestAnimationFrame(transAnim)
                else {
                    el.style.opacity = '1'
                    el.style.transform = ''
                }
            }

            transAnim()

        }
        //console.log(ssecsRemaining)
        //document.querySelector('#countdown > h1').textContent = 
        lastTimer = ssecsRemaining
        if (secsRemaining > 1000) requestAnimationFrame(updateTimer)
        else {
            document.querySelector<HTMLDivElement>('#countdown').style.transform = 'translateY(100vh)'
            startFireworks()
        }
    }

    requestAnimationFrame(updateTimer)

}


const yearDelays = [
    142,
    1142,
    412,
    813
]

abstract class Particle {
    x: number;
    y: number;

    vx: number;
    vy: number;

    constructor(x: number, y: number) {
        this.x = x
        this.y = y
    }

    step(dt: number) {
        this.x += this.vx * dt
        this.y += this.vy * dt

        this.vx *= 1 - dt
        this.vy *= 1 - dt
    }

    abstract draw(ctx: CanvasRenderingContext2D)
}

class Rocket extends Particle {
    private maxHeight: number;

    constructor(x: number, y: number) {
        super(x, y)
        this.vx = 0
        this.vy = -100
        this.maxHeight = Math.random() * 100 + 200
    }

    step(dt: number) {
        super.step(dt)
        this.vy -= 10
    }

    draw(ctx: CanvasRenderingContext2D) {
        ctx.strokeStyle = 'rgb(255, 100, 70)'
        ctx.beginPath()
        ctx.moveTo(this.x, this.y)
        ctx.lineTo(this.x, this.y + 10)
        ctx.stroke()
    }

    shouldExplode(): boolean {
        return this.y < this.maxHeight
    }
}

class Spark extends Particle {
    color: Color; // rgba 
    lifetime: number;
    age = 0;
    constructor(x: number, y: number, color: Color) {
        //console.log(color.toString(16))
        super(x, y)
        const angle = Math.random() * Math.PI * 2
        const force = Math.pow(Math.random(), 2) * 200
        this.vx = Math.sin(angle) * force
        this.vy = Math.cos(angle) * force
        this.color = color
        this.lifetime = Math.pow(Math.random(), 1) * 3 + 1 
    }

    draw(ctx: CanvasRenderingContext2D) {
        
        ctx.fillStyle = this.color.toString()
        ctx.fillRect(this.x, this.y, 2, 2)
    } 

    step(dt: number) {
        super.step(dt)
        this.vy += 1
        this.age += dt
        const alpha = 1 - (this.age / this.lifetime)
        this.color.a = alpha
    }
}

class Color {
    r: number;
    g: number;
    b: number;
    a: number
    constructor(r: number, g: number, b: number, a?: number) {
        this.r = r
        this.g = g
        this.b = b
        this.a = a || 1.
    }

    toString(): string {
        return this.a == 1 ? `rgb(${Math.round(this.r)}, ${Math.round(this.g)}, ${Math.round(this.b)})` 
        : `rgba(${Math.round(this.r)}, ${Math.round(this.g)}, ${Math.round(this.b)}, ${this.a})`
    }
}

function startFireworks() {
    console.log('fw')
    const canvas = document.querySelector<HTMLCanvasElement>('#fireworks canvas')
    canvas.width = window.innerWidth
    canvas.height = window.innerHeight
    const ctx = canvas.getContext('2d')
    for (let i = 0; i < yearDelays.length; i++) {
        setTimeout(() => {
            yearElements[i].style.transform = ''
        }, yearDelays[i])
    }

    let particles: Spark[] = []
    let rockets: Rocket[] = []


    function spawnRocket(x: number) {
        rockets.push(new Rocket(x, canvas.height + 20))
    }

    for (let i = 0; i < 4; i++) {
        spawnRocket(Math.random() * canvas.width)
    }
    ctx.strokeStyle = 'red'
    ctx.fillRect(100, 100, 100, 100)
    console.log(rockets)
    const dt = 1 / 60
    function draw() {
        
        ctx.fillStyle = 'rgba(0, 0, 0, 0.1)'
        ctx.fillRect(0, 0, canvas.width, canvas.height)

        //debugger
        for (const r of rockets) {
            //console.log(r.x, r.y)
            r.draw(ctx)
            r.step(dt)
            if (r.shouldExplode()) {
                const col = HSVtoRGB(Math.random(), 0.5, 1.)
                const bColor = new Color(col.r, col.g, col.b)
                //debugger
                for (let i = 0; i < 100; i++) {
                    particles.push(new Spark(r.x, r.y, bColor))
                }
            }
        }

        rockets = rockets.filter(r => !r.shouldExplode())

        if (Math.random() > 0.95) {
            spawnRocket(Math.random() * canvas.width)
        }

        for (const sp of particles) {
            sp.draw(ctx)
            sp.step(dt)
        }

        particles = particles.filter(p => p.age < p.lifetime)
        
        requestAnimationFrame(draw)
    }


    requestAnimationFrame(draw)

}
